h2, p {
  margin: 0;
  padding: 0;
}

img {
  width: 300px;
  object-fit: cover;
  display: block;
}

.cat-info {
  padding: 10px;
  display: flex;
}

.description, .info_tittle {
  margin-bottom: 10px;
}

.content {
  margin-left: 30px;
}

.temperament {
  font-weight: 700;
}

.loader {
  margin-top: 15px;
  display: flex;
}

.loader_text {
  color: #0b7e1c;
  margin-left: 15px;
  font-size: 20px;
  font-weight: 600;
}

.loader_svg {
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  border: 3px solid #0b7e1c;
  border-color: #0b7e1c rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: #d92161;
  font-size: 20px;
  font-weight: 600;
}

